@use "base/variables" as v;
@use "base/mixins" as m;

#toggleIcon {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.icon-animate {
    transform: scale(1.3);
    opacity: 0.7;
}

.table-scroll {
    height: 90vh;
    max-width: 144rem;
    overflow-y: auto;
    overflow-x: auto;
}

.table-container {
    margin: auto;
    width: 100%;
    table {
        table-layout: fixed;
        width: fit-content;
        border-collapse: collapse;
        max-height: 50vh;
        td {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
}

.ips-register {
    table {
        button {
            background-color: transparent;
            border: none;
        }

        .addBtn {
            border: 2px solid v.$black;
            border-radius: 5rem;
            width: 2.5rem;
            height: 2.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}