@use "variables" as v;
@use "base/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  font-family: v.$text;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

// Text

h2 {
  color: v.$pink;
  font-weight: bold;
}

// Form

.form-control {
  margin-bottom: 1rem;
}

.form-check-label {
  display: flex;
  align-items: center;
  input {
    margin-right: 0.5rem;
  }
}

// Buttons

.btn-pink, .btn-blue {
  padding: 0.5rem 2rem;
  border-radius: 10rem;
}

.btn-pink {
  background-color: v.$pink;
  color: v.$white;
  transition: background-color 0.3s ease-in-out;
  &:hover {
    background-color: darken(v.$pink, 10%);
    color: v.$white;
    transition: background-color 0.3s ease-in-out;
  }
}

.btn-blue {
  text-decoration: none;
  background-color: v.$blue;
  color: v.$white;
  transition: background-color 0.3s ease-in-out;
  &:hover {
    background-color: darken(v.$blue, 10%);
    color: v.$white;
    transition: background-color 0.3s ease-in-out;
  }
}
