@use "base/variables" as v;
@use "base/mixins" as m;

.baby-cloud-home {
    background-color: v.$gray;
    background-image: url('../../../build/img/bg/babyCloud-bg.webp');
    margin: 0;
    background-repeat: no-repeat;
    background-position: top center;
    background-attachment: fixed;
    background-size: 100% auto, contain;
    padding-bottom: calc(15vh + 2rem);

    img {
        width: calc(15vw + 10rem);
        margin-top: calc(10vh + 2rem);
        margin-bottom: 5rem;
        @include m.mobile {
            margin-bottom: 0;
        }
    }

    .links {
        display: flex;
        flex-direction: column;

        @include m.mobile {
            flex-direction: row;
        }

        &>* {
            margin: auto 2rem;
            display: flex;
            flex-direction: column;
            align-items: center;

            img {
                width: calc(3vw + 3rem);
                margin-bottom: 2rem;
                margin-top: 3rem;
                @include m.mobile {
                    margin-top: calc(5vh + 2rem);
                }
            }
        }
    }
}