@use "base/variables" as v;
@use "base/mixins" as m;

.baby-sites-home {
    background-color: v.$gray;
    .d-flex:first-child {
        img {
            width: calc(15vw + 10rem);
        }
        margin-left: 3.5rem;
        margin-top: 3.5rem;
    }
    h2 {
        text-align: center;
        margin-top: 7.5rem;
        margin-bottom: 2.5rem;
        @include m.tablet {
            margin-top: 10rem;
            margin-bottom: 5rem;
        }
    }
    .d-flex:last-child {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {
            flex-direction: row;
        }
        a {
            transition: transform 0.3s ease-in-out;
            &:hover {
                transform: scale(1.1);
                
            }
        }
        img {
            width: calc(15vw + 10rem);
            margin: auto 3.5vw;
        }
    }
}