@use "base/variables" as v;
@use "base/mixins" as m;

.login {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    img,
    .login-form {
        width: 45vw;
        margin: 0 auto;
    }
    .login-form {
        padding: 2rem;
        border: 2px solid v.$gray;
    }
}