html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  font-family: "Montserrat";
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  color: #ea6b9c;
  font-weight: bold;
}

.form-control {
  margin-bottom: 1rem;
}

.form-check-label {
  display: flex;
  align-items: center;
}
.form-check-label input {
  margin-right: 0.5rem;
}

.btn-pink, .btn-blue {
  padding: 0.5rem 2rem;
  border-radius: 10rem;
}

.btn-pink {
  background-color: #ea6b9c;
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}
.btn-pink:hover {
  background-color: #e43e7e;
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}

.btn-blue {
  text-decoration: none;
  background-color: #0da7cd;
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}
.btn-blue:hover {
  background-color: #0a809d;
  color: #ffffff;
  transition: background-color 0.3s ease-in-out;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.login img,
.login .login-form {
  width: 45vw;
  margin: 0 auto;
}
.login .login-form {
  padding: 2rem;
  border: 2px solid #fafafa;
}

.baby-cloud-home {
  background-color: #fafafa;
  background-image: url("../../../build/img/bg/babyCloud-bg.webp");
  margin: 0;
  background-repeat: no-repeat;
  background-position: top center;
  background-attachment: fixed;
  background-size: 100% auto, contain;
  padding-bottom: calc(15vh + 2rem);
}
.baby-cloud-home img {
  width: calc(15vw + 10rem);
  margin-top: calc(10vh + 2rem);
  margin-bottom: 5rem;
}
@media (min-width: 320px) {
  .baby-cloud-home img {
    margin-bottom: 0;
  }
}
.baby-cloud-home .links {
  display: flex;
  flex-direction: column;
}
@media (min-width: 320px) {
  .baby-cloud-home .links {
    flex-direction: row;
  }
}
.baby-cloud-home .links > * {
  margin: auto 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.baby-cloud-home .links > * img {
  width: calc(3vw + 3rem);
  margin-bottom: 2rem;
  margin-top: 3rem;
}
@media (min-width: 320px) {
  .baby-cloud-home .links > * img {
    margin-top: calc(5vh + 2rem);
  }
}

.baby-sites-home {
  background-color: #fafafa;
}
.baby-sites-home .d-flex:first-child {
  margin-left: 3.5rem;
  margin-top: 3.5rem;
}
.baby-sites-home .d-flex:first-child img {
  width: calc(15vw + 10rem);
}
.baby-sites-home h2 {
  text-align: center;
  margin-top: 7.5rem;
  margin-bottom: 2.5rem;
}
@media (min-width: 481px) {
  .baby-sites-home h2 {
    margin-top: 10rem;
    margin-bottom: 5rem;
  }
}
.baby-sites-home .d-flex:last-child {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 481px) {
  .baby-sites-home .d-flex:last-child {
    flex-direction: row;
  }
}
.baby-sites-home .d-flex:last-child a {
  transition: transform 0.3s ease-in-out;
}
.baby-sites-home .d-flex:last-child a:hover {
  transform: scale(1.1);
}
.baby-sites-home .d-flex:last-child img {
  width: calc(15vw + 10rem);
  margin: auto 3.5vw;
}

#toggleIcon {
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.icon-animate {
  transform: scale(1.3);
  opacity: 0.7;
}

.table-scroll {
  height: 90vh;
  max-width: 144rem;
  overflow-y: auto;
  overflow-x: auto;
}

.table-container {
  margin: auto;
  width: 100%;
}
.table-container table {
  table-layout: fixed;
  width: fit-content;
  border-collapse: collapse;
  max-height: 50vh;
}
.table-container table td {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ips-register table button {
  background-color: transparent;
  border: none;
}
.ips-register table .addBtn {
  border: 2px solid #000000;
  border-radius: 5rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}